/* Add these to your existing index.css */
.ripple-effect {
  position: fixed;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  pointer-events: none;
  z-index: 9999;
  transform: scale(0);
  animation: ripple 0.6s ease-out;
}

.dark-ripple {
  background: radial-gradient(circle, rgba(124, 58, 237, 0.7) 0%, rgba(99, 102, 241, 0.5) 100%);
}

.light-ripple {
  background: radial-gradient(circle, rgba(251, 113, 133, 0.7) 0%, rgba(253, 186, 116, 0.5) 100%);
}

@keyframes ripple {
  to {
    transform: scale(4);
    opacity: 0;
  }
}